<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <tg-list :IsShowAction="true" :showCheckBox="true" :listType="['List']" ModuleName="Asset"
            SubModuleCode="Product_Assignment" IdentityColumn="ASSIGNMENT_DETAIL_ID" HeaderText="Product Assignment"
            :ListData="AssetAssignmentData" :HeaderData="Headers" :widgets="widgets" :callbackfunction="FetchData"
            :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords" :SearchFields="leftSearchFields"
            :NorecordfoundText="$t('NoRecordfound')" :RenderRowActionMethod="renderActions"
            :ListDataCallBackFunction="FetchData" :LegendArray="LegendArray" :SortExp="SortExp" :SortBy="SortBy"
            v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
            v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:ActionButtonClick="actionButtonClick"
            v-on:SortdataButtonClick="sortdata">
            <template v-slot:slotdata="{ data }">

                <template v-if="data.column.COLUMN_NAME == 'UNIQUE_NAME'">
                    <span v-if="data.row.CREATE_UNIQUE_NAME_LIST == false" class="text-dark"
                        :title="data.column.COLUMN_NAME">
                    </span>
                    <span v-else class="text-dark" :title="data.column.COLUMN_NAME">
                        {{ data.row.UNIQUE_NAME }}
                    </span>
                </template>

                <template v-if="data.column.COLUMN_NAME == 'NUMBER_OF_ITEMS_ASSIGNED'">
                    <span v-if="data.row.CREATE_UNIQUE_NAME_LIST == false" class="text-dark"
                        :title="data.column.COLUMN_NAME">
                        <i class="fa fa-list-ul" aria-hidden="true"></i><span class="noti-circle noti-blue">{{
                            data.row.NUMBER_OF_ITEMS_ASSIGNED }}</span>
                    </span>
                    <span v-else class="text-dark" :title="data.column.COLUMN_NAME">
                        <i class="fa fa-list-ul" aria-hidden="true"></i><span class="noti-circle noti-blue">{{
                            data.row.NUMBER_OF_ITEMS_ASSIGNED }}</span>
                    </span>
                </template>

                <template v-if="data.column.COLUMN_NAME == 'ASSIGNED_FROM'">
                    <span class="text-dark" :title="data.column.COLUMN_NAME">
                        {{ formatTimeToCustomFormat(data.row.ASSIGNED_FROM) }}
                    </span>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'ASSIGNED_TILL'">
                    <span class="text-dark" :title="data.column.COLUMN_NAME">
                        {{ formatTimeToCustomFormat(data.row.ASSIGNED_TILL) }}
                    </span>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'RETURN_DATE'">
                    <span class="text-dark" :title="data.column.COLUMN_NAME">
                        {{ formatTimeToCustomFormat(data.row.RETURN_DATE) }}
                    </span>
                </template>

            </template>
        </tg-list>
        <ReturnPopup v-if="ReturnProductPopup" @CloseReurnProductPopup="CloseReurnProductPopup"
            :assDetailIDs="assDetailIDs">
        </ReturnPopup>
        <NotReturnedPopup v-if="NotReturnedPopup" @CloseNotReurnedProductPopup="CloseNotReurnedProductPopup"
            :selectedUserDetail="selectedUserDetail"></NotReturnedPopup>

        <ReturnForRequest v-if="IsRequestForReturn" @CloseReturnPopup="CloseReturnPopup"
            :IsRequestForReturnData="IsRequestForReturnData"></ReturnForRequest>

        <CommentPopUp v-if="isVisibleComponentPopup" @CloseCommentPopup="CloseCommentPopup" :assDetailIDs="assDetailIDs">
        </CommentPopUp>
        <SharePopUp v-if="isSharedPopupOpen" @CloseSharePopup="CloseSharePopup" :assignmentIdDetail="assDetailIDs">
        </SharePopUp>
        <ApproveReject v-if="ApproveRejectpopup" @CloseApproveRejectopup="CloseApproveRejectopup"
            :assDetailIDs="assDetailIDs"></ApproveReject>

        <RequestForReturnProduct v-if="isRequestForReturnProduct"
            @CloseRequestForReturnProduct="CloseRequestForReturnProduct" :assDetailIDs="assDetailIDs" :newValue="newValue">
        </RequestForReturnProduct>
    </section>
</template>

<script>
import DataService from "../../service/DataService";
import Schema from "../ProductAssignment/AssignmentSchema";
import ReturnPopup from "../../components/Modal/ReturnProductPopup.vue";
import CommentPopUp from "../../components/Modal/CommentPopUp.vue";
import ReturnForRequest from "../../components/Modal/ReturnForRequest.vue";
import SharePopUp from "../../components/Modal/SharePopUp.vue";
import NotReturnedPopup from "../../components/Modal/NotReturnedPopup.vue"
import ApproveReject from "../../components/Modal/ApproveRejectPopup.vue";
import RequestForReturnProduct from "../../components/Modal/RequestForReturnProduct.vue";
import { DateTime } from 'luxon';
export default {
    components: {
        NotReturnedPopup,
        ReturnPopup,
        CommentPopUp,
        ReturnForRequest,
        SharePopUp,
        RequestForReturnProduct,
        ApproveReject
    },
    data() {
        return {
            View_List: [],
            isLoading: false,
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            listActions: Schema.listActions,
            LegendArray: [],
            Headers: [{
                COLUMN_NAME: "NAME",
                DATA_TYPE: "text",
                SORTABLE: true,
                VISIBLE: true,
                DISPLAY_NAME: this.$t("AssetId"),
                DISPLAY_ORDER: 1,
            },
            {
                COLUMN_NAME: "ITEM_CODE",
                DATA_TYPE: "text",
                SORTABLE: true,
                VISIBLE: true,
                DISPLAY_NAME: this.$t("ProductCode"),
                DISPLAY_ORDER: 2,
            },
            {
                COLUMN_NAME: "UNIQUE_NAME",
                DATA_TYPE: "text",
                SORTABLE: true,
                VISIBLE: true,
                DISPLAY_NAME: this.$t("UniqueName"),
                DISPLAY_ORDER: 3,
                settings: {
                    isInSlot: true,
                },
            },
            {
                COLUMN_NAME: "NUMBER_OF_ITEMS_ASSIGNED",
                DATA_TYPE: "text",
                SORTABLE: true,
                VISIBLE: true,
                DISPLAY_NAME: this.$t("NumberOfItems"),
                DISPLAY_ORDER: 4,
                settings: {
                    isInSlot: true,
                },
            },
            {
                COLUMN_NAME: "LOCATION_NAME",
                DATA_TYPE: "text",
                SORTABLE: true,
                VISIBLE: true,
                DISPLAY_NAME: this.$t("LocationName"),
                DISPLAY_ORDER: 5,
            },
            {
                COLUMN_NAME: "ISSUED_TO",
                DATA_TYPE: "text",
                SORTABLE: true,
                VISIBLE: true,
                DISPLAY_NAME: this.$t("IssuedTo"),
                DISPLAY_ORDER: 6,
            },
            {
                COLUMN_NAME: "ISSUED_BY",
                DATA_TYPE: "text",
                SORTABLE: true,
                VISIBLE: true,
                DISPLAY_NAME: this.$t("IssuedBy"),
                DISPLAY_ORDER: 7,
            },
            {
                COLUMN_NAME: "ASSIGNED_FROM",
                DATA_TYPE: "Date",
                SORTABLE: true,
                VISIBLE: true,
                DISPLAY_NAME: this.$t("AssignedFrom"),
                DISPLAY_ORDER: 8,
                settings: {
                    isInSlot: true,
                },
            },
            {
                COLUMN_NAME: "ASSIGNED_TILL",
                DATA_TYPE: "Date",
                SORTABLE: true,
                VISIBLE: true,
                DISPLAY_NAME: this.$t("AssignedTill"),
                DISPLAY_ORDER: 9,
                settings: {
                    isInSlot: true,
                },
            },
            {
                COLUMN_NAME: "RETURN_DATE",
                DATA_TYPE: "Date",
                SORTABLE: true,
                VISIBLE: true,
                DISPLAY_NAME: this.$t("ReturnDate"),
                DISPLAY_ORDER: 10,
                settings: {
                    isInSlot: true,
                },
            },
            ],
            noRecord: true,
            TotalRecords: 0,
            CurrentPage: 1,
            TotalPages: 0,
            SortBy: "",
            SortExp: "",
            widgets: [],
            legendProgress: [{
                class: "",
                backgroundColor: "#17a2b8",
                name: "Draft",
            },
            {
                class: "legend-Progress",
                backgroundColor: "",
                name: "Approved"
            },
            {
                class: "legend-pending",
                backgroundColor: "",
                name: "Pending",
            },
            {
                class: "",
                backgroundColor: "#fe6847",
                name: "SendForCorrection",
            },
            {
                class: "",
                backgroundColor: "#ee1313",
                name: "Rejected",
            },
            ],
            ProductData: [],
            LeadStatusList: [],
            PageSize: 10,
            PageNumber: 1,
            isPaged: 1,
            AssetAssignmentData: [],
            ReturnProductPopup: false,
            isVisibleComponentPopup: false,
            isSharedPopupOpen: false,
            NotReturnedPopup: false,
            IsRequestForReturn: false,
            ApproveRejectpopup: false,
            isRequestForReturnProduct: false,
            assDetailIDs: "",
            canRequestforReturn: 0,
            requestForReturnData: [],
            newValue: '',
            selectedUserDetail: "",
            IsRequestForReturnData: {},
            Userid: null,
            privilegeParams: null,
            UserDateTime: new Date().toISOString().slice(0, 19)
        };
    },
    created: function () {
        var ths = this;
        if (window.location.href.split('/')[5] != undefined) {
            ths.assDetailIDs = window.location.href.split('/')[5]
        } else {
            ths.assDetailIDs = ""

        }
        ths.privilegeParams = `controller=AssetAssignment&action=`;
        Schema.bindleftCommonSearchdropdown(ths, "ASSETASSIGNMENT", "IssueBy");
        ths.Userid = this.GetUserInfo.ID;
        DataService.CheckPrivilege(ths.privilegeParams + 'Index').then((response) => {
            if (response.data == true) {
                ths.FetchData();
            } else {
                ths.ShowAlert(ths.$t('NotAuthorizedSection'), "failure", true, "Alert");
            }
        });
        ths.LegendArray = [{
            class: 'legend-pending colorboxsquare',
            text: ('Overdue')
        }]
    },


    methods: {
        async FetchData() {
            var vueObj = this;
            vueObj.isLoading = true;
            var params = `pageSize=${vueObj.PageSize}&pageNum=${vueObj.PageNumber}&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&isReturn=&issuedTo=&assetCatalogId=${vueObj.assDetailIDs}&uniqueNames=&showWithoutPaging=&barcode=&UserStatus=&${vueObj.searchCondition}`
            await DataService.AssetAssignmentListing(params).then((response) => {
                vueObj.isLoading = false;
                if (response.data != null && response.data != 0) {
                    if (response.data.length > 0) {
                        vueObj.AssetAssignmentData = response.data;
                        vueObj.AssetAssignmentData.forEach((row) => {
                            row.additionalClass = '';
                            var GetCurrentDate = new Date();
                            var IsOverdue = false;
                            //row.ASSIGNED_TILL != null ? ((row.IS_RETURN == false && vueObj.$options.filters.formatDate(row.ASSIGNED_TILL) < vueObj.$options.filters.formatDate(GetCurrentDate)) ? true : false) : false;
                            if (((vueObj.$options.filters.formatDateTimeYYMMDD(GetCurrentDate)) > (vueObj.$options.filters.formatDateTimeYYMMDD(row.ASSIGNED_TILL))) && row.IS_RETURN == false) {
                                IsOverdue = true;
                            } else {
                                IsOverdue = false;
                            }
                            if (IsOverdue) {
                                row.additionalClass = 'high-bar';
                            }
                        });
                        for (var row in vueObj.AssetAssignmentData) {
                            vueObj.AssetAssignmentData[row].isCheckBoxDisabled = false;
                            if (vueObj.AssetAssignmentData[row].IS_RETURN == true) {
                                vueObj.AssetAssignmentData[row].isCheckBoxDisabled = true;
                            }
                        }
                        vueObj.TotalRecords = response.data[0].TOTAL;
                        vueObj.TotalPages = Math.ceil(
                            vueObj.TotalRecords / vueObj.PageSize
                        );
                        vueObj.CurrentPage = vueObj.PageNumber;
                        $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                        vueObj.noRecord = false;
                    } else {
                        vueObj.noRecord = true;
                        vueObj.AssetAssignmentData = [];
                        vueObj.TotalRecords = 0;
                        vueObj.isLoading = false;
                    }
                } else {
                    vueObj.noRecord = true;
                    vueObj.AssetAssignmentData = [];
                    vueObj.TotalRecords = 0;
                    vueObj.isLoading = false;
                }
                setTimeout(function () {
                    vueObj.CheckBoxBootstrap();
                }, 100);
                setTimeout(function () {
                    vueObj.ResponsiveDataTable("tablelistingdata");
                }, 500);
            });
            vueObj.isLoading = false;
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            var vm = this;
            self.listActions.forEach((actionItem) => {
                let action = {
                    ...actionItem
                };
                switch (action.title) {
                    case "Share":
                        if (row.CREATE_UNIQUE_NAME_LIST && row.IS_RETURN) {                           
                            if (vm.formatTimeToCustomFormat(row.ASSIGNED_TILL) > vm.formatTimeToCustomFormat(vm.UserDateTime)) {
                                action.isDisabled = true;
                            }
                            else {
                                action.isDisabled = true;
                            }
                        } else {                          
                            if (row.IS_RETURN) {
                                action.isDisabled = true;
                            } else {
                                if (vm.formatTimeToCustomFormat(row.ASSIGNED_TILL) > vm.formatTimeToCustomFormat(vm.UserDateTime)) {
                                action.isDisabled = true;
                              }else{
                                action.isDisabled = false;
                              } 
                              
                            }
                        }
                        break;
                    case "Accept Reject Request":
                        if (row.RETURN_REQUEST == 'YES') {
                            if (row.IS_RETURN == false) {
                                action.isDisabled = false;
                                action.isVisible = true;
                            } else {
                                action.isDisabled = true;
                                action.isVisible = false;
                            }
                        } else if (row.RETURN_REQUEST == 'NO') {
                            action.isDisabled = true;
                            action.isVisible = false;
                        }
                        break;
                    case "Returned":

                        if (row.RETURN_REQUEST == 'YES') {
                            if (row.IS_RETURN == false) {
                                action.isDisabled = true;
                                action.isVisible = false;
                            } else {
                                action.isDisabled = false;
                                action.isVisible = true;
                            }
                        } else if (row.RETURN_REQUEST == 'NO') {
                            if (row.IS_RETURN == true) {
                                action.isDisabled = true;
                                action.isVisible = true;
                            } else {
                                action.isDisabled = true;
                                action.isVisible = false;
                            }
                        }

                        break;
                    case "Not Returned":
                        if (row.RETURN_REQUEST == 'NO') {
                            if (row.IS_RETURN == true) {
                                action.isDisabled = true;
                                action.isVisible = false;
                            } else {
                                action.isDisabled = false;
                                action.isVisible = true;
                            }
                        }
                        break;
                    case "Requst For Return":
                        if (row.IS_RETURN == false && row.RETURN_REQUEST == "NO" && row.ISSUED_TO_ID != vm.Userid) {
                            if (row.REQUEST_ID == 0) {
                                action.isDisabled = false;
                                action.isVisible = true;
                            } else {
                                action.isDisabled = true;
                                action.isVisible = false;
                            }
                        } else {
                            action.isDisabled = true;
                            action.isVisible = true;
                        }

                        break;
                    case "Request Already Sent":
                        if (row.IS_RETURN == false && row.RETURN_REQUEST == "NO" && row.ISSUED_TO_ID != vm.Userid) {
                            if (row.REQUEST_ID == 0) {
                                action.isDisabled = true;
                                action.isVisible = false;
                            } else {
                                action.isDisabled = false;
                                action.isVisible = true;
                            }
                        } else {
                            action.isDisabled = false;
                            action.isVisible = false;
                        }
                        break;
                    case "Comment":
                        if (row.IS_RETURN == true && (row.TITLE != '' && row.TITLE != null) || (row.COMMENT != '' && row.COMMENT != null)) {
                            action.isDisabled = false;
                        } else {
                            action.isDisabled = true;
                        }
                        break;
                    default: {
                        action.isDisabled = false;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchConditionJson;
            } else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "AssignProduct":
                    this.AssignProduct();
                    break;
                case "ReturnProduct":
                    this.ReturnProduct();
                    break;
                case "RequestForReturnProduct":
                    this.RequestForReturnProduct();
                    break;
            }
        },
        actionButtonClick: function (callbackfunction, item) {
            var vm = this;
            switch (callbackfunction) {
                case "Share":
                    vm.ShareButtonClick(item)
                    break;
                case "AcceptRejectRequest":
                    vm.openApproveRejectpopup(item.RELATED_INFO_ID);
                    break;
                case "Returned":
                    vm.NotReturnedProduct(item);
                    break;
                case "NotReturned":
                    vm.NotReturnedProduct(item);
                    break;
                case "RequestForReturn":
                    vm.RequestForReturn(item);
                    break;
                case "RequestAlreadySent":
                    break;
                case "Comment":
                    vm.commentButtonClick(item.ASSIGNMENT_DETAIL_ID);
                    break;
            }
        },
        commentButtonClick: function (ths) {
            this.assDetailIDs = ths;
            this.isVisibleComponentPopup = true;
        },

        ShareButtonClick: function (item) {
            this.assDetailIDs = item;
            this.isSharedPopupOpen = true;
        },

        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                } else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                } else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.FetchData();
            $("#" + s + "  span:first-child").attr("class", SortDir);
        },
        getRequestParams: function (
            pageNum,
            pageSize,
            sortBy,
            sortExp,
            isPaged,
            searchcondition
        ) {
            let params = {};
            if (pageNum) {
                params["pageNum"] = pageNum;
            }
            if (searchcondition) {
                params["searchcondition"] = searchcondition;
            }
            if (isPaged) {
                params["isPaged"] = isPaged;
            }
            if (sortBy) {
                params["sortBy"] = sortBy;
            }
            if (sortExp) {
                params["sortExp"] = sortExp;
            }
            if (pageSize) {
                params["pageSize"] = pageSize;
            }
            return params;
        },
        AssignProduct: function () {
            DataService.CheckPrivilege(this.privilegeParams + 'Add').then((response) => {
                if (response.data == true) {
                    this.$router.push({
                        name: "AddAssetAssignment",
                        params: {
                            reqForm: 'AssetAssignment'
                        }
                    });
                } else {
                    this.ShowAlert(this.$t('NotAuthorizedSection'), "failure", true, "Alert");
                }
            });
        },

        ReturnProduct: function (event) {
            var vm = this;
            DataService.CheckPrivilege(vm.privilegeParams + 'ItemReturnMultiple').then((response) => {
                if (response.data == true) {
                    var val = "";
                    $(".chkItems:checkbox:checked")
                        .not("[id^='chkAll']")
                        .each(function () {
                            if (val.length > 0) val += ",";
                            val += $(this).val();
                        });
                    if (val == "") {
                        vm.ShowAlert(
                            vm.$t("Minimum One Product should be selected."),
                            "failure",
                            vm.$t("Alert")
                        );
                        return false;
                    } else {

                        vm.assDetailIDs = val;
                        vm.ReturnProductPopup = true;
                    }
                } else {
                    vm.ShowAlert(vm.$t('NotAuthorizedSection'), "failure", true, "Alert");
                }
            });
        },
        openApproveRejectpopup(ths) {
            var vm = this;
            vm.assDetailIDs = ths;
            vm.ApproveRejectpopup = true;
        },

        RequestForReturnProduct() {
            var vm = this;
            let val = [];
            let notAvailable = '';
            let assignmentDetailIDs = '';
            DataService.CheckPrivilege(vm.privilegeParams + 'ItemRequstForReturnMultiple').then((response) => {
                if (response.data == true) {
                    $(".chkItems:checkbox:checked").not("[id^='chkAll']").each(function () {
                        val.unshift(vm.AssetAssignmentData.filter(dealer => dealer.ASSIGNMENT_DETAIL_ID == this.value));
                    });

                    for (let i in val) {
                        for (var j in val[i]) {
                            assignmentDetailIDs = val[i][j].ASSIGNMENT_DETAIL_ID;
                            if (vm.newValue != "") vm.newValue += ",";
                            vm.newValue += assignmentDetailIDs;

                            if (this.canRequestforReturn == "0") {

                                if (val[i][j].CREATE_UNIQUE_NAME_LIST == false) {
                                    // 
                                } else {
                                    if (notAvailable != "") notAvailable += ",";
                                    notAvailable += val[i][j].UNIQUE_NAME;
                                }

                            }
                        }
                    }

                    if (notAvailable != "null" && notAvailable != "") {
                        return vm.ShowAlert(
                            vm.$t(`Return Request still not approved ${notAvailable}`),
                            "warning",
                            vm.$t("Alert")
                        );
                    }
                    if (val == "") {
                        return vm.ShowAlert(
                            vm.$t("Minimum One Product should be selected."),
                            "warning",
                            vm.$t("Alert")
                        );
                    }
                    // this.GetAssignmentDetailByID(vm.newValue);
                    vm.isRequestForReturnProduct = true;
                } else {
                    this.ShowAlert(this.$t('NotAuthorizedSection'), "failure", true, "Alert");
                }
            });

        },
        CloseReurnProductPopup: function () {
            this.ReturnProductPopup = false;
        },
        CloseCommentPopup: function () {
            this.isVisibleComponentPopup = false;
        },
        CloseSharePopup: function () {
            this.isSharedPopupOpen = false;
        },
        RequestForReturn: function (ths) {
            debugger;
            this.IsRequestForReturnData = ths;
            this.IsRequestForReturn = true;
        },
        CloseReturnPopup: function () {
            this.IsRequestForReturn = false;
        },
        CloseApproveRejectopup: function () {
            var vm = this;
            vm.ApproveRejectpopup = false;
        },
        CloseRequestForReturnProduct: function () {
            this.isRequestForReturnProduct = false;
        },
        CloseNotReurnedProductPopup: function () {
            this.NotReturnedPopup = false;
        },
        NotReturnedProduct: function (value) {
            this.selectedUserDetail = value;
            this.NotReturnedPopup = true;
        },


        formatTimeToCustomFormat(inputTime) {
            if (inputTime != null && inputTime != '') {
                const inputLuxonDateTime = DateTime.fromISO(inputTime, { zone: 'utc' }); // Assume the input time is in UTC
                const targetTimeZone = Intl.DateTimeFormat().resolvedOptions().timeZone;

                const outputLuxonDateTime = inputLuxonDateTime.setZone(targetTimeZone);

                const formattedDateTime = outputLuxonDateTime.toFormat("MM/dd/yyyy h:mm a");
                return formattedDateTime;
            } else {
                return "--"
            }

        }

    }
};
</script>

<style>
span#action-list {
    display: flex;
    align-items: center;
}
</style>
