<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">
                            {{ $t('Return') }}
                        </h5>
                        <button v-on:click="CloseReurnProductPopup($event)" href="javascript:;" class="close"><i
                                class="fa fa-times" aria-hidden="true"></i></button>
                    </div>
                    <div class="modal-body">
                        <div class="listing">
                            <div class="table-responsive tableborder" style="overflow-x:auto;" v-if="!noRecordForTab">
                                <table class="table-sorting table table-bordered" id="table-openactivities-content"
                                    style="width:1500px !important;">
                                    <thead class="thead-bg">
                                        <tr>
                                            <th><span>{{ $t('ProductName') }}</span></th>
                                            <th><span>{{ $t('UniqueName') }} </span></th>
                                            <th><span>{{ $t('LocationName') }} </span></th>
                                            <th><span>{{ $t('IssuedTo') }} </span></th>
                                            <th><span>{{ $t('IssuedBy') }} </span></th>
                                            <th><span>{{ $t('AssignedFrom') }}</span></th>
                                            <th><span>{{ $t('AssignedTill') }} </span></th>
                                            <th><span>{{ $t('NumberofProducts') }} </span></th>
                                            <th><span>{{ $t('Quantity') }} </span></th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr v-for="(item, index) in AssignmentData" :key="index">
                                            <td>{{ item.AssetName }}</td>
                                            <td>{{ item.UniqueName }}</td>
                                            <td>{{ item.LocationName }}</td>
                                            <td>{{ item.IssuedToName }}</td>
                                            <td>{{ item.IssuedByName }}</td>
                                            <td>{{ item.AssignedFrom }}</td>
                                            <td>{{ item.AssignedTill }}</td>
                                            <td>
                                                <span class="text-dark" :title="item.NumberOfItemsToAssign"><em
                                                        class="fa fa-list" aria-hidden="true"></em><span
                                                        class="noti-circle noti-blue">{{ item.NumberOfItemsToAssign
                                                        }}</span></span>
                                            </td>
                                            <td>
                                                <input v-if="Boolean(item.HasUniqueNames)"  type="number" class="form-control"
                                                    v-model="item.quantity" v-on:input="checkQuantity(item)" v-on:keypress="preventMinusSign($event)" />
                                                <input v-else type="number" class="form-control"
                                                v-on:input="checkQuantity(item)"  v-model="item.quantity" v-on:keypress="preventMinusSign($event)" />
                                            </td>
                                        </tr>
                                        <div v-if="noRecordForTab" class="text-center text-danger p-4 border no-record">
                                            {{ $t('NoRecordfound') }}</div>
                                    </tbody>
                                </table>
                            </div>
                        </div>
                    </div>
                    <div id="dvReturnStatus" class="form-content mt-2 p-3" style="width:1200px !important;">
                        <Form ref="ReturnForm">
                            <div class="collapse show py-3">
                                <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left px-0">
                                    <div class="form-group">
                                        <span>{{ $t('Title:') }}</span>
                                        <input type="text" name="txtTitle" v-model="txtTitle" class="form-control"
                                            maxlength="150" />
                                    </div>
                                </div>
                                <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left px-0">
                                    <div class="form-group">
                                        <span> {{ $t('Comment') }}</span>
                                        <textarea name="txtComment" v-model="txtComment" class="form-control"
                                            maxlength="400"></textarea> 
                                    </div>
                                </div>
                                <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left px-0">
                                    <div class="row flex-row-reverse">
                                        <div class="col-lg-6 text-right">
                                            <a href="javascript:;" v-on:click="SubmitData()"
                                                class="btn btn-success formbtn text-white mr-1" :title="$t('Save')"><i
                                                    class="fa fa-save pr-2"></i>
                                                <span>{{ $t('Save') }}</span></a>
                                            <a href="javascript:;" @click="onCancel()" :title="$t('Cancel')"
                                                class="btn btn-danger formbtn text-white"><i
                                                    class="fa fa-close pr-2"></i><span>{{ $t('Cancel') }}</span></a>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </Form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataService from '../../service/DataService';
export default {
    props: {
        assDetailIDs: {
            type: String,
            required: true
        }
    },
    data() {
        return {
            noRecordForTab: false,
            isLoading: false,
            AssignmentData: [],
            txtTitle: '',
            txtComment: '',
            selectedItems:'',
            isQuantityValid: true,
        }
    },
    created: function () {
        this.GetAssignmentDetailByID();
    },
    methods: {
        preventMinusSign(event) {
        if (event.key === '-' || event.key === '.' || event.key === '+') {
            event.preventDefault();
        }
    },
        CloseReurnProductPopup: function (item) {
            this.$emit('CloseReurnProductPopup', item)
        },
        onCancel: function () {
            this.CloseReurnProductPopup();
        },
        async GetAssignmentDetailByID() {
            debugger
            var url = `assDetailIDs=${this.assDetailIDs}`
            await DataService.GetAssignmentDetailByID(url).then(response => {
                if (response.data != null) {
                    this.AssignmentData = response.data;
                    this.AssignmentData.forEach(ele=>{
                        ele.quantity=ele.NumberOfItemsToAssign;
                    })
                    this.noRecordForTab = false;
                } else {
                    this.noRecordForTab = true;
                }
            });
        },
         SubmitData() {
            var vm = this;
            var detail = "", realtedinfoIDs = "", status = true, qty = '';
            vm.$refs.ReturnForm.validate().then(result => {
                if (result) {
                    vm.AssignmentData.forEach(ele => {
                        vm.checkQuantity(ele);
                   vm.qty = ele.quantity;
                        vm.assDetailID = ele.AssignmentDetailId;
                        vm.userid = ele.IssuedToId;
                        vm.RelatedInfoID = ele.RelatedInfoID;
                        vm.NumberOfItemsToAssign = ele.NumberOfItemsToAssign;
                        vm.UniqueName = ele.UniqueName;
                        if (detail != "") detail += "|";
                        detail += vm.assDetailID + "," + vm.qty + "," + vm.userid;
                        if (realtedinfoIDs != "") realtedinfoIDs += "|";
                        realtedinfoIDs += vm.assDetailID + "," + vm.RelatedInfoID + "," + qty + "," + vm.UniqueName;
                    });
                    if (detail == "") {
                        vm.ShowAlert(vm.$t("Minimum One Product should be selected."), "warning", vm.$t('Alert'));
                        // hideLoader();
                        return false;
                    }
                    if (status == false || !vm.isQuantityValid) {
                        return false;
                    }
                    var data = { itemdetails: detail, title: vm.txtTitle, comment: vm.txtComment, relatedInfoIDs: realtedinfoIDs }
                    var jsonObj = JSON.stringify(data);
                     DataService.BulkReturnItem(jsonObj).then(response => {
                        if (response.data != null) {                         
                            if (response.data.result == true) {
                                if(response.data.msg[0].MessageCode=='Success'){
                                    vm.ShowAlert(vm.$t("Item(s) are returned successfully."), "success", vm.$t('Alert'));
                                }else{
                                    vm.ShowAlert(vm.$t(response.data.msg[0].MessageCode), "success", vm.$t('Alert'));
                                }                                
                                vm.$parent.FetchData();
                                vm.CloseReurnProductPopup();
                            }
                        }
                    });
                }
            })

        },                    
                
         
        // }
        checkQuantity(item) {            
            var vm = this;
           
            if(item.quantity!=''){
                vm.selectedItems = vm.AssignmentData
                    .filter(ele => ele.AssetAssignmentId === item.AssetAssignmentId);
                if (item.NumberOfItemsToAssign !== 0 && item.NumberOfItemsToAssign !== null && item.NumberOfItemsToAssign !== '' && item.NumberOfItemsToAssign < parseInt(item.quantity)) {
                    vm.ShowAlert(vm.$t('Please enter a quantity less than or equal to Number Of Products.'), "warning", true, vm.$t("Alert")) ;
                    vm.isQuantityValid = false;
                
                }
                else{
                    vm.isQuantityValid = true;
                }
            }            
        }
    },

}
</script>