<template>
  <div class="my-popups">
    <loader :is-visible="isLoading"></loader>
    <div class="modal d-block">
      <div class="modal-dialog modal-dialog-centered">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">
              {{ $t("Product Sharing") }}
            </h5>
            <button v-on:click="CloseSharePopup($event)" href="javascript:;" class="close">
              <i class="fa fa-times" aria-hidden="true"></i>
            </button>
          </div>

          <div class="modal-body">
            <div class="row">
              <div class="col-sm-12 col-lg-6">
                <div class="form-group">
                  <label>{{ $t("IssuedTo") }} :
                    <span class="detailtxt">{{ assignmentIdDetail.ISSUED_TO }}</span></label>
                </div>
              </div>
              <div class="col-sm-12 col-lg-6">
                <div class="form-group">
                  <label>{{ $t("Product") }} :
                    <span class="detailtxt">{{ assignmentIdDetail.NAME }}</span></label>
                </div>
              </div>
              <div class="col-sm-12 col-lg-6">
                <div class="form-group">
                  <label>{{ $t("UniqueName") }} :
                    <span class="detailtxt">{{ assignmentIdDetail.UNIQUE_NAME }}</span></label>
                </div>
              </div>
              <div class="col-sm-12 col-lg-6">
                <div class="form-group">
                  <label>{{ $t("AssignedFrom") }} :
                    <span class="detailtxt">{{
                      $options.filters.formatDateTime(
                        assignmentIdDetail.ASSIGNED_FROM
                      )
                    }}</span>
                  </label>
                </div>
              </div>
              <div class="col-sm-12 col-lg-6">
                <div class="form-group">
                  <label>{{ $t("AssignedTill") }} :
                    <span class="detailtxt">{{
                      $options.filters.formatDateTime(
                        assignmentIdDetail.ASSIGNED_TILL
                      )
                    }}</span></label>
                </div>
              </div>
            </div>

            <Form v-slot="{ valid, errors }" ref="addEmployeform">
              <div class="row">
                <div class="col-sm-12 col-md-6 col-lg-4 col-xl-3 float-left">
                  <div class="form-group">
                    <label>{{ $t('Location') }}:<span class="text-danger">*</span></label>
                    <Field name="Location" class="w-100" :rules="{ 'required': true }" v-slot="{ errors }">
                      <tree-view v-model="TreeValue" :options="options" placeholder="Select Location" :show-count="true"
                        v-bind:class="{ 'form-control': true, 'is-invalid': errors[0] }">
                      </tree-view>
                      <span v-if="errors[0]" name="Location" class="invalid-feedback d-block">{{ errors[0] }}</span>

                    </Field>
                  </div>
                </div>
                <div class="col-sm-12 col-md-6 col-lg-4 col-xl-3 float-left">
                  <div class="form-group" style="position: relative;">

                    <label> {{ $t('EmployeeCode') }}:<span class="text-danger">*</span></label>
                    <Field name="name">
                      <input @keyup="getEmploye()" v-model="txtEmployeNameCode" type="text"
                        v-bind:class="{ 'form-control': true }" />

                    </Field>
                    <div class="unique_dynamicdatalist">

                      <ul v-for="(item, index) in employe" @click="setUserName(item.USERNAME, item.USER_ID)" :key="index">
                        <li v-if="showProductDropdown" style="text-transform: capitalize;" :value="item.USER_ID">{{
                          item.USERNAME }}</li>
                      </ul>

                    </div>
                  </div>

                </div>
                <div class="col-12 col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left">
                  <div class="search-btm-btn background-0 text-left py-4">
                    <a href="javascript:;" @click="addemployeInTable(valid, errors)"
                      class="btn btn-success float-left "><i class="fa fa-plus mr-2 "></i>{{ $t('Add') }}</a>
                  </div>
                </div>
              </div>
            </Form>


            <div class="listing mt-2 mb-2">
              <div class="table-responsive">
                <table id="tblEmployeeList" class="table table-bordered  nowrap mt-0">
                  <thead class="thead-bg">
                    <tr>
                      <th class="ui-resizable" style="width: 228px; min-width: 228px; max-width: 228px;"> {{
                        $t('EmployeeCode') }} <div class="sizer"></div>
                        <div class="ui-resizable-handle ui-resizable-e" style="z-index: 90;"></div>
                      </th>
                      <th class="text-center ui-resizable" style="width: 85px; min-width: 85px; max-width: 85px;"> {{
                        $t("Action") }}
                        <div class="sizer"></div>
                        <div class="ui-resizable-handle ui-resizable-e" style="z-index: 90;"></div>
                      </th>
                    </tr>
                  </thead>
                  <tbody>

                    <tr v-if="employeNameCode == null || employeNameCode == ''">
                      <td class="text-center text-danger p-4 border no-record">
                        {{ $t('NoRecordfound') }}</td>
                    </tr>

                    <tr v-else>
                      <td>{{ employeNameCode }}</td>

                      <td class="text-center"><a href="javascript:;" @click="RemoveRow(employeNameCode)" title="Delete"><i
                            class="recyclebin fa fa-trash action-icon text-danger inactive"></i></a></td>
                    </tr>

                  </tbody>
                </table>
              </div>
            </div>
            <div class="col-lg-12 float-left p-0">
              <div class="row flex-row-reverse">
                <div class="col-lg-12 text-right">
                  <a class="btn btn-success" v-on:click="saveEmploye()"><i class="fa fa-save pr-2"></i>{{ $t('Save')
                  }}</a>&nbsp;
                  <a href="javascript:;" class="btn btn-danger" v-on:click="CloseSharePopup($event)"><i
                      class="fa fa-close mr-2"></i>{{
                        $t('Cancel') }}</a>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import DataService from "../../service/DataService";
import Treeselect from '@riophae/vue-treeselect'
import '@riophae/vue-treeselect/dist/vue-treeselect.css'
export default {
  components: {
    'tree-view': Treeselect
  },
  props: {
    assignmentIdDetail: {
      type: Object,
      required: true,
    },
  },

  data() {
    return {
      LocationData: [],
      txtEmployeNameCode: '',
      employeNameCode: '',
      noRecordForTab: false,
      employe: '',
      isValueShow: '',
      showProductDropdown: false,
      UserId: '',
      msg: {},
      selectedValue: '',
      TreeValue: null,
      options: []
    };
  },
  created() {
    this.GetLocationByuserId();
  },
  methods: {
    CloseSharePopup: function (item) {
      this.$emit("CloseSharePopup", item);
    },

    async GetLocationByuserId() {
      var vm = this;
      vm.FindalLocationArray = [];
      await DataService.GetLocationByuserId()
        .then(response => {
          vm.LocationData = response.data.data;
          vm.LocationData.forEach((value) => {
            var CheckIfExist = vm.FindalLocationArray.filter(function (el) {
              return el.COMPANY_LOCATION_ID == value.COMPANY_LOCATION_ID;
            });
            if (CheckIfExist.length == 0) {
              if (value.PARENT_LOCATION_ID == null) {
                let locationId = value.COMPANY_LOCATION_ID
                let rslt = vm.getChildLocation(locationId, value, response.data.data)
                vm.FindalLocationArray.push(rslt)
              }
            }
          });
          vm.options = vm.FindalLocationArray;

        });
    },
    getChildLocation: function (locationId, currentItem, data) {
      let ths = this;
      let treeArray = {};
      treeArray = {
        id: currentItem.COMPANY_LOCATION_ID,
        label: currentItem.LOCATION_NAME
      }
      let retrunArray = treeArray;
      var childArray = data.filter(function (el) {
        return el.PARENT_LOCATION_ID == locationId;
      });
      let finalChildArray = [];
      childArray.forEach((value) => {
        let ChildfolderId = value.COMPANY_LOCATION_ID
        var CheckIfExist1 = finalChildArray.filter(function (el) {
          return el.COMPANY_LOCATION_ID == value.COMPANY_LOCATION_ID;
        });
        if (CheckIfExist1.length == 0) {
          var CheckIfExist = data.filter(function (el) {
            return el.PARENT_LOCATION_ID == ChildfolderId;
          });
          if (CheckIfExist != null && CheckIfExist.length > 0) {
            let rslt = ths.getChildLocation(ChildfolderId, value, data);
            finalChildArray.push(rslt);
          } else {
            treeArray = {
              id: `${value.COMPANY_LOCATION_ID}`,
              label: `${value.LOCATION_NAME}`
            }
            finalChildArray.push(treeArray);
          }
        }
      });
      retrunArray.children = finalChildArray;
      return retrunArray;
    },


    setUserName: function (ths, id) {
      debugger;
      this.txtEmployeNameCode = ths;
      this.selectedValue = this.txtEmployeNameCode;
      this.UserId = id;
      this.showProductDropdown = false;

    },

    addemployeInTable: function (event) {
      debugger;
      var vm = this;
      vm.$refs.addEmployeform.validate().then(result => {
        if (vm.TreeValue == "") {
          return
        }
        if (vm.txtEmployeNameCode == "") {
          return vm.ShowAlert(
            vm.$t("Please Select an Employee"),
            "failure",
            vm.$t("Alert")
          );
        }

        if (vm.txtEmployeNameCode == vm.selectedValue) {
          vm.employeNameCode = vm.txtEmployeNameCode;
          this.txtEmployeNameCode = null;

        }
        else {
          vm.txtEmployeNameCode = null;
          vm.employeNameCode = null;
          vm.ShowAlert(
            vm.$t("Please Select an Employee"),
            "failure",
            vm.$t("Alert")
          );
        }

      })

    },
    RemoveRow: function () {
      this.employeNameCode = null;

    },
    getEmploye() {
      var vm = this;
      vm.$refs.addEmployeform.validate().then(result => {
        const url = `name=${this.txtEmployeNameCode}&locationId=${this.TreeValue}&modulename=assetassignment&relatedInfoId=${this.assignmentIdDetail.RELATED_INFO_ID}`;
        DataService.GetEmployeeAutoComplete(url).then((response) => {
          if (response.data != null) {
            this.employe = response.data;
            this.showProductDropdown = true;
          } else {
            this.employe = "";
          }
        });

      })
    },

    async saveEmploye() {

      var vm = this;
      if (this.UserId == "" || this.employeNameCode == null) {
        return vm.ShowAlert(
          vm.$t("No Employee selected, please select an Employee to share this Product."),
          "failure",
          vm.$t("Alert")
        );
      }

      else {
        const url = `relatedInfoId=${this.assignmentIdDetail.RELATED_INFO_ID}&IssuedToId=${this.assignmentIdDetail.ISSUED_TO_ID}&employeeIds=${this.UserId}`;

        await DataService.ItemSharing(url).then((response) => {
          console.log("response", response);
          if (response.data != null) {
            this.msg = response.data[0];
            if (this.msg.STATUS == 100) {
              this.CloseSharePopup();
              vm.ShowAlert(
                vm.$t("Item shared successfully."),
                `${this.msg.MESSAGE}`,
                vm.$t("Alert")
              );
            }
            else {
              this.CloseSharePopup();
              vm.ShowAlert(
                vm.$t("Error occur while Item Shared "),
                `${this.msg.MESSAGE}`,
                vm.$t("Alert")
              );
            }
          }
        });
      }
    },

  },
};

</script>

<style scoped>
.unique_dynamicdatalist {
  border: none;
}

.vue-treeselect__control {
  border: none;
  height: 24px;
  padding: 0;
}

.vue-treeselect--searchable .vue-treeselect__input-container {
  padding: 0 !important;
}

.vue-treeselect__placeholder {
  color: #303030 !important;
}

.vue-treeselect__control-arrow {
  color: #000;
}

.vue-treeselect__placeholder {
  line-height: 24px;
}
</style>
